<template>
  <a-menu :selected-keys="selectKey" class="topbar" mode="horizontal">
    <a-menu-item
        id="logo"
        key="0"
        :style="{ padding: 0, marginRight: '38px' }"
        disabled
    >
      <!-- <div
        :style="{
          width: '80px',
          height: '30px',
          borderRadius: '2px',
          background: 'var(--color-fill-3)',
          cursor: 'text',
        }"
      /> -->
    </a-menu-item>
    <div id="items">
      <a-menu-item key="home" @click="routerPush('/', 'home')"> {{ $t("home") }}</a-menu-item>
      <a-menu-item key="buy" @click="routerPush('/car/buy', 'buy')"> {{ $t("buy") }}</a-menu-item>
      <a-menu-item key="sell" @click="routerPush('/car/sell', 'sell')"> {{ $t("sell") }}</a-menu-item>
      <a-menu-item key="right" >
        <a-button class="right" type="outline" status="danger" @click="clickRight"></a-button>
      </a-menu-item>
      <a-menu-item key="avatar">
        <a-dropdown trigger="hover">
          <a-avatar class="avatar" id="avatar">
            
            <!--{{ $t("unlogin") }}-->
          </a-avatar>
          <template #content>
            <a-doption @click="routerPush('/login')">{{ $t("login") }}</a-doption>
            <a-doption @click="routerPush('/register')">{{ $t("register") }}</a-doption>
          </template>
        </a-dropdown>
      </a-menu-item>
    </div>
  </a-menu>
</template>

<script>
export default {
  name: "TopBar",
  data() {
    return {
      selectTopItem: "",
      selectKey: ""
    }
  },
  components: {},
  methods: {
    routerPush(path, key) {
      // storekey
      key = "['" + key + "']";
      this.selectKey = key;
      this.$store.commit('setBuySelectedKey', key);
      this.$router.push(path);
    },
    setSelectedKey() {
      let nowKey = this.$store.getters.getBuySelectedKey;
      // storekeyhome
      if (nowKey == "") {
        nowKey = "['home']"
      }
      this.selectKey = nowKey;
    },
    clickRight() {
      this.$notification.warning({content: ''})
    }
  },
  mounted() {
    this.setSelectedKey()
  }
};
</script>

<style scoped>
.topbar {
  position: sticky;
  box-sizing: border-box;
  width: 100vw;
  height: 8vh;
  border-top-style: solid;
  border-top-width: 5px;
  border-top-color: #4080ff;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #c9cdd4;
  top: 0;
  z-index: 999;
}

.arco-menu-selected-label {
  display: none;
}

#logo {
  float: left;
}

#items {
  float: right;
}

.avatar {
  font-size: 20px;
}
