export default {
  name: "HomeCard",
  props: ['title', 'imagePath', 'path', 'topKey'],
  methods: {
    routerPush() {
      // let key = "['" + this.topKey + "']";
      // alert(key)
      this.$store.commit('setBuySelectedKey', this.topKey);
      this.$router.replace(this.path);
    },
  }
};
</script>

