<template>
  <a-space class="space" direction="vertical" fill size="large">
    <div v-if="current === 1">
      <a-typography-title :heading="4" class="title"></a-typography-title>

      <a-select v-model="selectedBrand" :style="{width:'20vw'}" allow-search placeholder=": " size="large">
        <a-option v-for="brand in carBrands" :key="brand" :value="brand"> {{ brand }}</a-option>
      </a-select>

      <div class="buttons">
        <a-button size="large" type="outline" @click="handleBack"></a-button>
        <a-button class="next" size="large" type="primary" @click="handleNext"></a-button>
      </div>
    </div>

    <div v-if="current === 2">
      <a-typography-title :heading="4" class="title"></a-typography-title>

      <a-select v-model="selectedSeries" :style="{width:'20vw'}" allow-search placeholder=": A3A5" size="large">
        <a-option v-for="series in carSeries" :key="series" :value="series"> {{ series }}</a-option>
      </a-select>

      <div class="buttons">
        <a-button size="large" type="outline" @click="handleBack"></a-button>
        <a-button class="next" size="large" type="primary" @click="handleNext"></a-button>
      </div>
    </div>

    <div v-if="current === 3">
      <a-typography-title :heading="4" class="title"></a-typography-title>

      <a-select v-model="selectedModel" :style="{width:'20vw'}" allow-search placeholder="..." size="large">
        <a-option v-for="model in carModels" :key="model" :value="model"> {{ model }}</a-option>
      </a-select>

      <div class="buttons">
        <a-button size="large" type="outline" @click="handleBack"></a-button>
        <a-button class="next" size="large" type="primary" @click="handleNext"></a-button>
      </div>
    </div>

    <div v-if="current === 4">
      <a-typography-title :heading="4" class="title"></a-typography-title>

      <a-space direction="vertical" size="large">
        <a-select v-model="selectedProvince" :style="{width:'20vw'}" allow-search placeholder=""
                  size="large" @change="handleProvinceChange">
          <a-option v-for="province in provinces" :key="province" :value="province"> {{ province }}</a-option>
        </a-select>

        <a-select v-model="selectedCity" :style="{width:'20vw'}" allow-search placeholder=""
                  size="large" @change="handleCityChange">
          <a-option v-for="city in cities" :key="city" :value="city"> {{ city }}</a-option>
        </a-select>

        <a-select v-model="selectedCounty" :style="{width:'20vw'}" allow-search placeholder=""
                  size="large" @change="handleCountyChange">
          <a-option v-for="county in counties" :key="county" :value="county"> {{ county }}</a-option>
        </a-select>

        <a-select v-model="selectedTown" :style="{width:'20vw'}" allow-search placeholder="" size="large">
          <a-option v-for="town in towns" :key="town" :value="town"> {{ town }}</a-option>
        </a-select>
      </a-space>

      <div class="buttons">
        <a-button size="large" type="outline" @click="handleBack"></a-button>
        <a-button class="next" size="large" type="primary" @click="handleNext"></a-button>
      </div>
    </div>

    <div v-if="current === 5" class="lastDiv">
      <a-typography-title :heading="6" class="titleOne">
        {{ this.selectedModel }}
        <a-link @click="handleChangeCar"></a-link>
      </a-typography-title>
      <a-typography-text class="titleOne">
        {{ this.selectedProvince + this.selectedCity + this.selectedCounty + this.selectedTown }}
        <a-link @click="handleChangeAddress"></a-link>
      </a-typography-text>
      <a-typography-title :heading="5" class="titleTwo"> </a-typography-title>

      <div class="cards">
        <a-card class="card">
          <a-typography-title :heading="6" class="cardTitleOne"></a-typography-title>
          <a-typography-title :heading="5" class="cardTitleTwo">139</a-typography-title>
          <a-typography-text class="cardTitle"></a-typography-text>

          <div class="cardContents">
            <div class="description">
              <icon-check class="check"/>
              <p></p>
            </div>
            <div class="description">
              <icon-check class="check"/>
              <p>6</p>
            </div>
            <div class="description">
              <icon-check class="check"/>
              <p></p>
            </div>
          </div>
          <a-button class="chooseLevel" shape="round" type="primary"></a-button>
        </a-card>

        <a-card class="card">
          <a-typography-title :heading="6" class="cardTitleOne"></a-typography-title>
          <a-typography-title :heading="5" class="cardTitleTwo">179</a-typography-title>
          <a-typography-text class="cardTitle"></a-typography-text>

          <div class="cardContents">
            <div class="description">
              <icon-check class="check"/>
              <p></p>
            </div>
            <div class="description">
              <icon-check class="check"/>
              <p>6</p>
            </div>
            <div class="description">
              <icon-check class="check"/>
              <p>2</p>
            </div>

          </div>
          <a-button class="chooseLevel" shape="round" type="primary"></a-button>
        </a-card>

        <a-card class="card">
          <a-typography-title :heading="6" class="cardTitleOne"></a-typography-title>
          <a-typography-title :heading="5" class="cardTitleTwo">219</a-typography-title>
          <a-typography-text class="cardTitle"></a-typography-text>

          <div class="cardContents">
            <div class="description">
              <icon-check class="check"/>
              <p>62</p>
            </div>
            <div class="description">
              <icon-check class="check"/>
              <p></p>
            </div>
            <div class="description">
              <icon-check class="check"/>
              <p></p>
            </div>
          </div>
          <a-button class="chooseLevel" shape="round" type="primary"></a-button>
        </a-card>
      </div>
    </div>

    <a-divider v-if="current !== 5"/>

    <a-steps v-if="current !== 5" :current=current>
      <a-step></a-step>
      <a-step></a-step>
      <a-step></a-step>
      <a-step></a-step>
      <a-step></a-step>
    </a-steps>
  </a-space>
</template>

<script>
import carBrandsJson from '../../assets/carBrands.json'
import carSeriesJson from '../../assets/carSeries.json'
import carModelsJson from '../../assets/carModels.json'
import provinceJson from '../../assets/province.json'
import cityJson from '../../assets/city.json'
import countyJson from '../../assets/county.json'
import townJson from '../../assets/town.json'
import {IconCheck} from '@arco-design/web-vue/es/icon'

export default {
  name: "CarSell",
  components: {IconCheck},
  data() {
    return {
      selectedBrand: undefined,
      selectedSeries: undefined,
      selectedModel: undefined,
      selectedProvince: undefined,
      selectedCity: undefined,
      selectedCounty: undefined,
      selectedTown: undefined,
      carBrands: [],
      carSeries: [],
      carModels: [],
      provinces: [],
      cities: [],
      counties: [],
      towns: [],
      current: 1
    }
  },
  methods: {
    handleBack() {
      if (this.current === 1) {
        this.$notification.error('')
      } else {
        this.current -= 1
      }
    },
    handleNext() {
      if (this.current === 1) {
        this.current += 1

        let newSeries = []
        for (let i in this.carSeries) {
          if (this.carSeries[i].includes(this.selectedBrand)) {
            newSeries.push(this.carSeries[i])
          }
        }

        this.carSeries = newSeries
      } else if (this.current === 2) {
        this.current += 1

        let newModels = []
        for (let i in this.carModels) {
          if (this.carModels[i].includes(this.selectedSeries)) {
            newModels.push(this.carModels[i])
          }
        }

        this.carModels = newModels
      } else {
        this.current += 1
      }
    },
    handleProvinceChange() {
      let cities = []
      for (let i in provinceJson) {
        if (provinceJson[i] === this.selectedProvince) {
          cities.push(cityJson[i])
        }
      }

      this.cities = new Set(cities)
    },
    handleCityChange() {
      let counties = []
      for (let i in cityJson) {
        if (cityJson[i] === this.selectedCity) {
          counties.push(countyJson[i])
        }
      }

      this.counties = new Set(counties)
    },
    handleCountyChange() {
      let towns = []
      for (let i in countyJson) {
        if (countyJson[i] === this.selectedCounty) {
          towns.push(townJson[i])
        }
      }

      this.towns = new Set(towns)
    },
    handleChangeCar() {
      this.current = 1
    },
    handleChangeAddress() {
      this.current = 4
    }
  },
  created() {
    this.carBrands = carBrandsJson
    this.carSeries = carSeriesJson
    this.carModels = carModelsJson
    this.provinces = new Set(provinceJson)
  }
};
</script>

