<script>
import { IconMessage, IconPhone, IconHeart, IconHeartFill, IconCheckCircleFill, IconCloseCircleFill, IconEye } from '@arco-design/web-vue/es/icon';

export default {
  name: "CarDetail",
  components: { IconMessage, IconPhone, IconHeart, IconHeartFill, IconCheckCircleFill, IconCloseCircleFill, IconEye },
  data() {
    return {
      images: [
        '',
      ],
      title: '',
      price: 0,
      mileage: '',
      carNum: '',
      visible: false,
      getPhoneVisible: false,
      carHistoryVisible: false,
      checkVisible: false,
      mainInfo: this.$t("mainInfo"),
      exteriors: this.$t("exteriors"),
      specifications: this.$t("specifications"),
      spaceSize: [0, 0],
      showHeart: true,
      showHeartFill: false,
      columns: [{
        title: 'Name',
        dataIndex: 'name'
      }, {
        title: 'Value',
        dataIndex: 'value'
      }],
      carData: [{
        name: '',
        value: '111'
      }, {
        name: '',
        value: '111'
      }, {
        name: '',
        value: '111'
      }, {
        name: '',
        value: '111'
      }, {
        name: '',
        value: '111'
      }, {
        name: '',
        value: '111'
      }, {
        name: '',
        value: '111'
      }, {
        name: '',
        value: '-'
      }]
    };
  },
  // props: ['title', 'price', 'mileage', 'carNum'],
  methods: {
    routerPush(path) {
      this.$router.push(path);
    },
    handleClick() {
      this.visible = true;
    },
    handleCancel() {
      this.visible = false;
    },
    clickGetPhone() {
      this.getPhoneVisible = true;
    },
    clickCheck() {
      this.checkVisible = true;
    },
    checkOk() {
      this.checkVisible = false;
    },
    cancelCheck() {
      this.checkVisible = false;
    },
    getPhoneOk() {
      this.getPhoneVisible = false;
    },
    cancelGetPhone() {
      this.getPhoneVisible = false;
    },
    back() {
      this.$router.back();
    },
    saveMouseOver() {
      this.showHeartFill = true;
      this.showHeart = false;
    },
    saveMouseLeave() {
      this.showHeartFill = false;
      this.showHeart = true;
    },
    clickCarHistory() {
      this.carHistoryVisible = true;
    },
    cancelCarHistory() {
      this.carHistoryVisible = false;
    }
  },
  mounted() {
    this.$nextTick(function () {
      // 
      let carDetail = this.$store.getters.getNowViewCar;
      this.title = carDetail.title
      this.price = carDetail.price
      this.mileage = carDetail.mileage

      this.carData[0]['value'] = carDetail.title
      this.carData[1]['value'] = carDetail.price + ' '
      this.carData[2]['value'] = carDetail.mileage
      this.carData[3]['value'] = carDetail.gearbox
      this.carData[4]['value'] = carDetail.body
      this.carData[5]['value'] = carDetail.engine
      this.carData[6]['value'] = carDetail.energy

      let imgPath = carDetail.imagePath
      this.images = [imgPath, imgPath, imgPath]
    })
  }
}
</script>

