<template>
  <a-layout>
    <a-layout-header>
      <advCard class="adv-top" title=""></advCard>
      <a-divider/>
    </a-layout-header>
    <a-layout>
      <a-layout-sider style="width: 30vw" theme="dark">
        <sidebar style="float: right" theme="light"/>
      </a-layout-sider>
      <a-layout-content style="width: 70vw">
        <div class="space">
          <div class="search">
            <icon-search class="searchIcon"/>
            <a-input v-model="searchValue" :style="{width:'320px'}" allow-clear
                     placeholder="" @press-enter="search" size="large"/>
            <a-button class="researchButton" size="large" type="primary" @click="search"></a-button>
          </div>
          <a-space class="tags">
            :
            <a-tag></a-tag>
            <a-tag></a-tag>
            <a-tag>SUV</a-tag>
            <a-tag></a-tag>
            <a-tag></a-tag>
          </a-space>
          <div v-for="(car, index) in carList" :key="index" :car="car" :index="index" class="carCard">
            <carCard
                :car=car
                :engine=car.engine
                :gearbox=car.gearbox
                :imagePath=car.imagePath
                :level=car.level
                :mileage=car.mileage
                :price=car.price
                :title=car.title
            >
            </carCard>
          </div>
        </div>

        <a-space direction="vertical" size="large" style="width: 15vw">
          <advCard class="adv-right" title=""></advCard>
        </a-space>
      </a-layout-content>
    </a-layout>
    <a-layout-footer></a-layout-footer>
  </a-layout>
</template>

<script>
import carDataJSON from '../../assets/carData.json'
import {IconSearch} from '@arco-design/web-vue/es/icon'

export default {
  name: "CarBuy",
  components: {IconSearch},
  data() {
    return {
      // a-col
      aColSpan: 0,
      carList: [],
      searchValue: ''
    };
  },
  methods: {
    search() {
      let newCarList = []
      for (let i in carDataJSON) {
        if (carDataJSON[i].title === "RS 4 2020 RS 4 2.9T Avant") {
          newCarList.push(carDataJSON[i])
        }
      }

      this.carList = newCarList
    }
  },
  created() {
    let carList = this.$store.getters.getCarList

    if (carList.length == 0) {
      this.$store.commit('setCarList', carDataJSON)
      carList = carDataJSON
    }
    this.carList = carList;

    let brands = new Set()
    for (let index in carDataJSON) {
      brands.add(carDataJSON[index].brand)
    }
    this.$store.commit('setBrands', brands)
  },
  mounted() {
    // storea-col
    this.aColSpan = this.$store.getters.getAColSpan
  }
};
</script>

