import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home'
import Login from '../views/Login'
import Register from '../views/Register'
import Car from '../views/car/Car'
import CarBuy from '../views/car/CarBuy'
import CarSell from '../views/car/CarSell'
import CarDetail from '../views/car/CarDetail'
import Chat from '../views/Chat'
import Ship from '../views/ship/Ship'
import ShipBuy from '../views/ship/ShipBuy'
import Plane from '../views/plane/Plane'
import PlaneBuy from '../views/plane/PlaneBuy'
import Motor from '../views/motor/Motor'
import MotorBuy from '../views/motor/MotorBuy'
import Bike from '../views/bike/Bike'
import BikeBuy from '../views/bike/BikeBuy'
import Trunk from '../views/trunk/Trunk'
import TrunkBuy from '../views/trunk/TrunkBuy'

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/car",
    name: "Car",
    component: Car,
    children: [
      {
        path: "buy",
        name: "CarBuy",
        component: CarBuy,
      },
      {
        path: "sell",
        name: "CarSell",
        component: CarSell,
      },
      {
        path: "detail",
        name: "CarDetail",
        component: CarDetail,
      }
    ],
  },
  {
    path: "/ship",
    name: "Ship",
    component: Ship,
    children: [
      {
        path: "buy",
        name: "ShipBuy",
        component: ShipBuy,
      }
    ]
  },
  {
    path: "/plane",
    name: "Plane",
    component: Plane,
    children: [
      {
        path: "buy",
        name: "PlaneBuy",
        component: PlaneBuy,
      }
    ]
  },
  {
    path: "/motor",
    name: "Motor",
    component: Motor,
    children: [
      {
        path: "buy",
        name: "MotorBuy",
        component: MotorBuy,
      }
    ]
  },
  {
    path: "/bike",
    name: "Bike",
    component: Bike,
    children: [
      {
        path: "buy",
        name: "BikeBuy",
        component: BikeBuy,
      }
    ]
  },
  {
    path: "/truck",
    name: "Truck",
    component: Trunk,
    children: [
      {
        path: "buy",
        name: "TrunkBuy",
        component: TrunkBuy,
      }
    ]
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/register",
    name: "Register",
    component: Register,
  },
  {
    path: "/chat",
    name: "Chat",
    component: Chat,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router
