import { createStore } from 'vuex'

export default createStore({
  state: {
    aColSpan: "6",
    buySelectedSort: "",
    buySelectedKey: "",
    carList: "",
    nowViewCar: "",
    brands: "",
    selectedBrands: "",
  },
  getters: {
    getAColSpan: (state) => {
      return state.aColSpan;
    },
    getBuySelectedSort: (state) => {
      return state.buySelectedSort;
    },
    getBuySelectedKey: (state) => {
      return state.buySelectedKey;
    },
    getCarList: (state) => {
      return state.carList;
    },
    getNowViewCar: (state) => {
      return state.nowViewCar
    },
    getBrands: (state) => {
      return state.brands
    },
    getSelectedBrands: (state) => {
      return state.selectedBrands
    },
  },
  mutations: {
    setMobileAColSpan: (state) => {
      state.aColSpan = "24";
    },
    setBuySelectedSort(state, sort) {
      state.buySelectedSort = sort;
    },
    setBuySelectedKey(state, key) {
      state.buySelectedKey = key;
    },
    setCarList(state, carList) {
      state.carList = carList
    },
    setNowViewCar(state, car) {
      state.nowViewCar = car
    },
    setBrands(state, brands) {
      state.brands = brands
    },
    setSelectedBrands(state, selectedBrands) {
      state.selectedBrands = selectedBrands
    }
  },
  actions: {},
  modules: {},
});
