  <a-menu
      :default-open-keys="['carBasicInfo']"
      :default-selected-keys="['0_2']"
      :style="{ width: '12vw' }"
      breakpoint="xl"
  >
    <div class="dropdown">
      <a-dropdown @select="selectSort">
        <a-button long type="outline"> {{ defaultSort }}
          <icon-down/>
        </a-button>
        <template #content>
          <a-doption> {{ $t("randomSort") }}</a-doption>
          <a-doption> {{ $t("mostFavorite") }}</a-doption>
          <a-doption> {{ $t("mostClick") }}</a-doption>
          <a-doption> {{ $t("priceLowToHigh") }}</a-doption>
          <a-doption> {{ $t("priceHighToLow") }}</a-doption>
          <a-doption> {{ $t("kmsLowToHigh") }}</a-doption>
          <a-doption> {{ $t("kmsHighToLow") }}</a-doption>
          <a-doption> {{ $t("yearLowToHigh") }}</a-doption>
          <a-doption> {{ $t("yearHighToLow") }}</a-doption>
        </template>
      </a-dropdown>
    </div>

    <div class="filter">
      <icon-filter class="iconFilter" size="20"/>
      <strong>{{ $t("filter") }}</strong>
    </div>

    <a-sub-menu key="carBasicInfo">
      <template #title>{{ $t("carBasicInfo") }}</template>

      <a-menu-item>
        {{ $t("location") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item @click="openModal">
        {{ $t("make") }}
        <icon-right class="iconRight"/>
        <a-modal :visible="modalVisible" @ok="okModal" @cancel="cancelModal">
          <template #title>
            
          </template>
          <a-checkbox-group v-model="checkedBox">
            <div class="checkbox" v-for="(brand, index) in brands" :brand="brand" :key="index">
              <a-checkbox :value="brand"> {{ brand }} </a-checkbox>

              <a-divider v-if="index < brandsSize - 1" class="dividerOfRadio"/>
            </div>
          </a-checkbox-group>
        </a-modal>
      </a-menu-item>

      <a-menu-item>
        {{ $t("price") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("year") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("odometer") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("energy") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("newSecondhand") }}
        <icon-right class="iconRight"/>
      </a-menu-item>
    </a-sub-menu>

    <a-divider></a-divider>

    <a-sub-menu key="carEngineInfo">
      <template #title>{{ $t("carEngineInfo") }}</template>

      <a-menu-item>
        {{ $t("driveType") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("cylinders") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("engineType") }}
        <icon-right class="iconRight"/>
      </a-menu-item>
    </a-sub-menu>

    <a-divider></a-divider>

    <a-sub-menu key="carStyleInfo">
      <template #title>{{ $t("carStyleInfo") }}</template>

      <a-menu-item>
        {{ $t("level") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("color") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("seats") }}
        <icon-right class="iconRight"/>
      </a-menu-item>

      <a-menu-item>
        {{ $t("doors") }}
        <icon-right class="iconRight"/>
      </a-menu-item>
    </a-sub-menu>
  </a-menu>
</template>

<script>
import {IconDown, IconFilter, IconRight} from "@arco-design/web-vue/es/icon";

export default {
  name: "SideBar",
  data() {
    return {
      defaultSort: "",
      modalVisible: false,
      brands: [],
      brandsSize: 0,
      checkedBox: []
    }
  },
  components: {
    IconRight,
    IconFilter,
    IconDown
  },
  methods: {
    selectSort(nowSort) {
      this.$store.commit("setBuySelectedSort", nowSort);
      this.defaultSort = nowSort;
    },
    setDefaultSort() {
      let nowSort = this.$store.getters.getBuySelectedSort;
      // store
      if (nowSort === "") {
        nowSort = this.$t("randomSort")
      }
      this.defaultSort = nowSort;
    },
    openModal() {
      this.modalVisible = true
    },
    okModal() {
      this.modalVisible = false

      let carList = this.$store.getters.getCarList
      let selectedBrands = this.checkedBox

      let newCarList = []

      selectedBrands.forEach(function (brand) {
        carList.forEach(function (car) {
          if (brand === car.brand) {
            newCarList.push(car)
          }
        })
      })

      this.$store.commit('setSelectedBrands', selectedBrands)
      if (newCarList.size === 0) {
        this.$store.commit('setCarList', "")
      } else {
        this.$store.commit('setCarList', newCarList)
      }
      this.$router.go(0)
    },
    cancelModal() {
      this.modalVisible = false
    }
  },
  mounted() {
    this.$nextTick(function () {
      this.setDefaultSort()

      let brands = this.$store.getters.getBrands
      this.brands = brands
      this.brandsSize = brands.size

      let selectedBrands = this.$store.getters.getSelectedBrands

      this.checkedBox = new Array(selectedBrands)
    })
  }
};
</script>

