import { createI18n } from "vue-i18n";
import zhCN from "./lang/zh"

const i18n = createI18n({
  locale: "zh-CN",
  globalInjection: true,
  fallbackLocale: "zh-CN",
  messages: {
    "zh-CN": zhCN,
  },
});

export default i18n;